
#tags{
	margin-left: 180px;
	position: relative;
	min-height: 30px;
	border-bottom: 2px solid #000;
	width: 5000px;
}
#tags .biaoqian{
	position: absolute;
	left: 1%;
	top:3px;
}
#tags .biaoqian .span{
	margin-right:10px;
	padding-left:5px;
	border:1px solid #000;
	border-color: rgba(32,160,255,.2);
	border-radius: 7px;
	font-size: 12px;
	line-height: 18px;
	color:#20a0ff;
	cursor: pointer;
	background: rgba(32,160,255,.1);
}
#tags .biaoqian .i{
	display:inline-block;
	text-align: center;
	font-size: 14px;
	line-height: 18px;
	width:18px;
	text-align: center;
}
#tags .biaoqian .i:hover{
	border-radius: 50%;
	background: rgba(32,160,255,.5)
}



