
#calendar{
	margin-left: 180px;
	min-width: 800px;
}
#calendar .app{
  color:green;
}
#calendar .events-day{
	text-align: center;
	position: relative;
}
#calendar .day-number{
	text-align: center;
	font-size: 24px;
	margin-top: 30%;
	height: 65%;
}
#calendar .event-box{
	position: absolute;
	width: 100%;
	height: 40px;
	left: 0;
	top: 0;
}
#calendar .event-item{
	line-height: 40px;
	height: 100%;
}



