
#carousel2{
    height: 300px;
    padding-top: 1%;
    margin: 0 auto;
}
#carousel2 .carousel-3d-container{
    margin: 0 auto;
    position: relative;
}
#carousel2 .carousel-3d-container img{
    height: 240px;
    width:280px;
    border: 3px solid #fff;
}
#carousel2 .title { 
    position: absolute;
    bottom: 30px;
    font-size: 22px;
    line-height: 30px;
    width:100%;
    color:#fff;
    background: #000;
    opacity: 0.5;
    text-align: center;
}
#carousel2 .message { 
    display: block;
    position: absolute;
    width:100%;
    bottom: 0;
    text-align: center;
    background: #000;
    opacity: 0.5;
    color:#fff;
    font-size: 15px;
    line-height: 30px;
}



