
#imgheader{
  margin-left: 180px;
  padding-top: 20px;
  position: relative;
}
#imgheader .btn{
	margin:0 auto;
	cursor: pointer;
	display: block;
	height: 30px;
	line-height: 30px;
	font-size: 20px;
	color:#000;
	width:100px;
	text-align: center;
	border: 1px solid #ccc;
	box-shadow:5px 5px 5px #ccc;
}
#imgheader .imgs{
	overflow: hidden;
	border: 1px solid #ccc;
	margin: 120px auto;
	width: 620px;
	height: 300px;
	position: relative;
}
#newimg{
	float: left;
	height: 300px;
	width:300px;
	box-shadow: 10px 10px 10px #ccc;
}
#newimg2{
	float: right;
	height: 300px;
	width:300px;
	border-radius: 50%;
	box-shadow: 10px 10px 10px #ccc;
}



