
#search{
    height: 100%;
    width:100%;
    position: relative;
    overflow: hidden;
}
#search .el-input__inner{
    text-indent: 8%;
}
#search li{
    text-indent: 10%;
}
#search  .liold{
    text-indent: 0;
    margin-top: 20%;
    text-align: center;
    font-size: 30px;
    cursor: pointer;
}
#search .liold:hover{
  color:#ccc; 
  -webkit-animation: neon2 1.5s ease-in-out infinite alternate; 
          animation: neon2 1.5s ease-in-out infinite alternate;
}
#search li a{
    width:100%;
    line-height: 24px;
    font-size: 14px;
    font-family: '';
    color:#000;
    text-shadow:4px 2px 2px #999;
    transition: all 0.5s;
}
#search li a:hover{
  color:#ffffff; 
  -webkit-animation: neon2 1.5s ease-in-out infinite alternate; 
          animation: neon2 1.5s ease-in-out infinite alternate;
}
@-webkit-keyframes neon2 {
from {
    text-shadow: 0 0 10px #fff,
               0 0 20px  #fff,
               0 0 30px  #fff,
               0 0 40px  #228DFF,
               0 0 70px  #228DFF,
               0 0 80px  #228DFF,
}
to {
    text-shadow: 0 0 5px #fff,
               0 0 10px #fff,
               0 0 15px #fff,
               0 0 20px #228DFF,
               0 0 35px #228DFF,
               0 0 40px #228DFF,
               0 0 50px #228DFF,
               0 0 75px #228DFF;
}
}



