
#leftdiv{
		position: fixed;
  	left: 0;
  	top: 0;
  	z-index: 1000;
  	width: 180px;
  	background: #2f4050;
  	bottom: 0;
  	transition:all 0.5s ease-out;
  	overflow: hidden;
  	cursor: pointer;
  	white-space: nowrap;
  	font-family:Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif
}
#leftdiv #leftdiv_1{
    position: absolute;
		top: 0;
		bottom: 0;
    width:100%;
    height: 150px;
    background: url(header-profile.png) no-repeat top left;
    background-size: 100% 100%;
}
#leftdiv #leftdiv_1 .leftdiv_1_header img{
    height: 70px;
    width:70px;
    border-radius: 50%;
    margin-top: 10%;
    margin-left: 20%;
    display: block;
}
#leftdiv #leftdiv_1 .leftdiv_1_header h4{
    height: 30px;
    line-height: 30px;
    color:#ccc;
    text-indent: 16%;
}
#leftdiv #leftdiv_1 .leftdiv_1_header h5 .el-dropdown{
  	text-indent: 5px;
  	padding: 0;
  	margin: 0;
}
.el-dropdown-menu .el-dropdown-menu__item{
  	text-align: center;
}
#leftdiv #leftdiv_1 .leftdiv_1_header h5{
    height: 30px;
    line-height: 20px;
    color:#ccc;
    text-indent: 16%;
}
#leftdiv #leftdiv_2{
		position: absolute;
		/*right: -17px;*/
		overflow-y: scroll;
		top: 150px;
		bottom: 0;
}
#leftdiv #el_tree{
  	width:180px;
  	border: 0;
  	background: #2f4050;
  	color:#bfcbd9;
}
#leftdiv #el_tree .el-tree-node__children{
  	background: #1f2d3d;
}
#leftdiv #el_tree .el-tree-node__content{
  	height: 40px;
  	padding: 5px;
  	padding-left: 10px;
}
#leftdiv #el_tree .el-tree-node__content:hover{
    background: #293846;
    color:#fff;
}
#leftdiv #el_tree .el-tree-node span{
  	line-height: 40px;
}



