
#message{
    height: 100%;
    width:100%;
    position: relative;
    overflow: hidden;
}
#message .message_header{
    height:15%;
    width:100%;
    border-bottom:1px solid #ccc;
    position: relative;
}
#message .message_header .message_header_nr{
    width:80px;
    height:20px;
    position:absolute;
    left:50%;
    top:50%;
    margin-top:-10px;
    margin-left: -40px;
}
.message_body li{
    height: 6%;
    padding: 2%;
    line-height: 16px;
    border-bottom: 1px solid #ccc;
}
.message_body li span{
    font-size: 12px;
    text-indent: 3%;
    display: inline-block;
}
.message_body img{
    height: 40px;
    width:40px;
    border-radius: 50%;
    display: inline-block;
    vertical-align:top;
}
#messageinput{
    position: absolute;
    bottom: 0;
    left:0;
    width:80%;
}
#messagebtn{
    position: absolute;
    bottom: 0;
    right:0;
    width:20%;
}



