
#ball3d{
    margin-left: 180px;
    margin-top: 7%;
}
#ball3d #sence{
    height: 400px;
    width:400px;
    margin: 0 auto;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-perspective:1000px;
            perspective:1000px;
    position: relative;
    border: 1px solid #000;
}
#ball3d #box{
    height: 100%;
    width:100%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
#ball3d #ring{
    width:100%;
    height: 100%;
    position: relative;
}
#ball3d #ring li{
    position: absolute;
    color:orangered;
    font-weight: 900;
}
#ball3d .all{
    transition: 1s ease;
}
#ball3d .one{
    transition: -webkit-transform 1s ease;
    transition: transform 1s ease;
    transition: transform 1s ease, -webkit-transform 1s ease;
}
#ball3d .newitem{
    position: absolute;
    width:100%;
    border: 1px solid #000;
    top:100px;
    -webkit-transform: scale(5);
            transform: scale(5);
    opacity: 0;
    display: none;
    transition: all 0.5s ease;
    -webkit-transform-origin: center center -200px;
            transform-origin: center center -200px;
}
#ball3d .newitem p{
    font-size: 14px;
    line-height: 24px;
    word-break: break-all;
}
#ball3d .newitem a{
    height: 19px;
    width:19px;
    background: url(images/xx.png) no-repeat center center;
    position: absolute;
    top:-30px;
    right: -30px;
}
#ball3d #table{
    width: 400px;
    margin: 20px auto 10px;
}
#ball3d #table a{
    float:left;
    width:25%;
    height: 12px;
    font-size: 12px;
    padding-top: 94px;
    line-height: 1px;
    font-family: "";
    text-align: center;
    color:#889199;
    background-position:center top;
}
#ball3d #table a:nth-of-type(1){
    background: url(images/yuan0.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(2){
    background: url(images/yuanzhui0.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(3){
    background: url(images/yuanzhu0.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(4){
    background: url(images/niuqu0.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(1):hover,#table a:nth-of-type(1).hover{
    background: url(images/yuan1.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(2):hover,#table a:nth-of-type(2).hover{
    background: url(images/yuanzhui1.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(3):hover,#table a:nth-of-type(3).hover{
    background: url(images/yuanzhu1.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(4):hover,#table a:nth-of-type(4).hover{
    background: url(images/niuqu1.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(1):active,#table a:nth-of-type(1).active{
    background: url(images/yuan2.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(2):active,#table a:nth-of-type(2).active{
    background: url(images/yuanzhui2.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(3):active,#table a:nth-of-type(3).active{
    background: url(images/yuanzhu2.png) no-repeat center top;
}
#ball3d #table a:nth-of-type(4):active,	#table a:nth-of-type(4).active{
    background: url(images/niuqu2.png) no-repeat center top;
}
#ball3d .line{
    width: 734px;
    height: 1px;
    margin: 0 auto 14px;
    background: -webkit-linear-gradient(left,#74cdf4,#8cca94,#fff45c);
}
#ball3d .newbtns{
    width:204px;
    height: 30px;
    margin: 0 auto;
}
#ball3d button{
    width:100px;
    height: 100%;
    border: 1px solid #cacaca;
    float:left;
    color:#889199;
    background: -webkit-linear-gradient(top,#fff,#d6e1eb);
}
#ball3d button:hover{
    color:#00a0e9;
    background: -webkit-linear-gradient(top,#bef5ff,#77c5eb);
}
#ball3d button:active{
    color:#889199;
    background: -webkit-linear-gradient(top,#77c5eb,#bef5ff);
}
#ball3d .active{
    color:#889199;
    background: -webkit-linear-gradient(top,#77c5eb,#bef5ff);
}
#ball3d .newbtns button:nth-of-type(1){
    border-radius:15px 0px 0px 15px;
}
#ball3d .newbtns button:nth-of-type(2){
    border-radius:0px 15px 15px 0px;
    margin-left: -1px;
}
#ball3d .text{
    width:602px;
    height: 318px;
    padding: 15px 18px;
    border: 1px solid #b3b8bb;
    position: absolute;
    left: 50%;
    margin-left: -329px;
    top:30px;
    background: #fff;
    border-radius: 8px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    opacity: 0;
    transition: all 0.5s ease;
    display: none;
}
#ball3d .text p{
    font-size: 12px;
    margin-bottom: 15px;
}
#ball3d .text textarea{
    resize: none;
    width:580px;
    height: 220px;
    padding: 10px;
    border: 1px solid #cacaca;
    border-radius: 8px;
    background: #f3f7fa;
    border-bottom: 15px;
}
#ball3d .btn2{
    width: 260px;
    height: 30px;
    margin: 0 auto;
}
#ball3d .btn2 button{
    width: 120px;
    height: 100%;
    border: 1px solid #cacaca;
    border-radius: 15px;
    float:left;
}
#ball3d .btn2 button:nth-of-type(1){
    margin-right: 15px;
}
#ball3d .close{
    position: absolute;
    top:10px;
    right: 10px;
    width:19px;
    height: 19px;
    background: url(images/xx.png) no-repeat center center;
}



