
.container {
    margin-left: 180px;
    padding-top: 20px;
    min-width: 800px;
}
.container code {
    display: block;
    padding: 8px 15px;
    background-color: #f6f8fa;
    border-radius: 5px;
    border: 1px solid #e5e5e5;
    overflow-x: auto;
    font-family: Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal;
    color: #333;
    font-size: 12px;
}
.container h3 {
    font-size: 36px;
    color: #494949;
    line-height: 1.1;
    text-align: center;
}
.container .github-corner:hover .octo-arm {
    -webkit-animation: octocat-wave 560ms ease-in-out;
            animation: octocat-wave 560ms ease-in-out
}
@-webkit-keyframes octocat-wave {
0%,
    100% {
        -webkit-transform: rotate(0);
                transform: rotate(0)
}
20%,
    60% {
        -webkit-transform: rotate(-25deg);
                transform: rotate(-25deg)
}
40%,
    80% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg)
}
}
@keyframes octocat-wave {
0%,
    100% {
        -webkit-transform: rotate(0);
                transform: rotate(0)
}
20%,
    60% {
        -webkit-transform: rotate(-25deg);
                transform: rotate(-25deg)
}
40%,
    80% {
        -webkit-transform: rotate(10deg);
                transform: rotate(10deg)
}
}
@media (max-width:500px) {
.github-corner:hover .octo-arm {
        -webkit-animation: none;
                animation: none
}
.github-corner .octo-arm {
        -webkit-animation: octocat-wave 560ms ease-in-out;
                animation: octocat-wave 560ms ease-in-out
}
}
.container .example-btn {
    display: inline-block;
    margin-bottom: 0;
    font-weight: 500;
    text-align: center;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    line-height: 1.5;
    padding: 4px 15px;
    font-size: 12px;
    border-radius: 4px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: all .3s cubic-bezier(.645, .045, .355, 1);
    position: relative;
    color: rgba(0, 0, 0, .65);
    background-color: #fff;
    border-color: #d9d9d9;
    left:48%;
}
.container .example-btn:hover {
    color: #4AB7BD;
    background-color: #fff;
    border-color: #4AB7BD;
}
.container .example-item {
    margin-bottom: 40px;
}
.container .example1 {
    font-size: 40px;
    color: #30B08F;
    display: block;
    margin: 10px 0;
    text-align: center;
}
.container .example2 {
    font-size: 40px;
    color: #E65D6E;
    display: block;
    margin: 10px 0;
    text-align: center;
}
.container .example3 {
    font-size: 50px;
    color: #F6416C;
    display: block;
    margin: 10px 0;
    text-align: center;
    font-size: 80px;
    font-weight: 500;
}
.container .label {
    color: #2f4f4f;
    font-size: 16px;
    display: inline-block;
    margin: 15px 30px 15px 0;
}
.container input {
    position: relative;
    display: inline-block;
    padding: 4px 7px;
    width: 50px;
    height: 28px;
    cursor: text;
    font-size: 12px;
    line-height: 1.5;
    color: rgba(0, 0, 0, .65);
    background-color: #fff;
    background-image: none;
    border: 1px solid #d9d9d9;
    border-radius: 4px;
    transition: all .3s;
}
.container .startBtn {
    margin-left: 20px;
    font-size: 20px;
    color: #30B08F;
    background-color: #fff;
}
.container .startBtn:hover {
    background-color: #30B08F;
    color: #fff;
    border-color: #30B08F;
}
.container .pause-resume-btn {
    font-size: 20px;
    color: #E65D6E;
    background-color: #fff;
}
.container .canshu{
	width: 800px;
	margin: 0 auto;
}
.container .pause-resume-btn:hover {
    background-color: #E65D6E;
    color: #fff;
    border-color: #E65D6E;
}



