
#photo{
  margin-left: 180px;
}
.photodiv{
  padding: 40px;
}
.cancelselect{
  -moz-user-select: none; /**/
  -webkit-user-select: none; /*webkit*/
  -ms-user-select: none; /*IE10*/ /**/
  user-select: none;
}
.zzc2{
  position: absolute;
  left: 0;
  top: 50px;
  height: 100%;
  width:100%;
  background: #000;
  opacity: 0.5;
}
.zzc{
  position: absolute;
  left: 0;
  top: 50px;
  height: 100%;
  width:100%;
}
.zzc .zzc_photo{
  width:800px;
  height: 600px;
  position: absolute;
  left:50%;
  top:50%;
  margin-left: -300px;
  margin-top: -330px;
  border: 5px solid #fff;
  border-radius: 10px;
  transition: 1s;
}
.zzc .zzc_photo a{
  width:38px;
  height: 38px;
  position: absolute;
  right: 0;
  top:0;
  background: url(fancybox_sprite.png) no-repeat;
  transition: 1s;
}
.zzc .zzc_photo img{
  width:100%;
  height: 100%;
  opacity: 1;
  border-radius: 10px;
}
#photo ul{
  overflow: hidden;
  height: 100%;
  width:100%;
}
#photo ul li{
  float:left;
  width:24%;
  margin: 0.5%;
  height:300px;
  box-shadow:5px 5px 10px #000;
  border-radius: 10px;
}
#photo ul li img{
  border-radius: 10px;
  height: 100%;
  width:100%;
}



