
body{
  	margin: 0;
  	padding: 0;
  	overflow-y: scroll;
}
#header_div{
  	margin-left: 180px;
  	min-width: 800px;
}
#header_div .tianqi{
  	width: 25%;
  	height: 30px;
  	line-height: 30px;
  	position: absolute;
  	left: 15%;
  	top: 15%;
  	text-align: center;
  	border: 2px solid #ccc;
  	border-radius: 5px;
  	box-shadow: 2px 2px 2px #ccc;
}
#header_div .pinglun{
		position: absolute;
		right:20px;
		top:0;
}
#header_div .pinglun .el-badge__content{
		top:10px;
		z-index: 10;
}
.el-dropdown-menu{
		top:20px;
}
#header_div .el-breadcrumb__item{
		height: 50px;
		line-height: 50px;
		font-size: 16px;
		margin-left: 20px;
}
#header_div .el-breadcrumb__item span{
		cursor: pointer
}
#header_div .el-row {
  	border-bottom: 2px solid #000;
}
#header_div .grid-content {
  	height: 50px;
  	line-height: 50px;
    min-height: 36px;
}



