
#talk{
    margin-left: 180px;
    background-size: 100% 100%;
    padding-top: 50px;
}
#talkcontent{
    width:1000px;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.12),0 0 6px 0 rgba(0,0,0,.04);
}
#talk input{
    font-size:14px; 
    line-height:24px; 
    color:#333; 
    font-family:Microsoft yahei, Song, Arial, Helvetica, Tahoma, Geneva;
}
#talk h1{
    margin-bottom:15px; 
    height:50px; 
    line-height:50px; 
    text-align:center; 
    font-size:24px; 
    color:#000; 
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
#talk #content #post,#comment p{
    zoom:1;
}
#talk #content #post:after,#comment p:after{
    display:block; 
    height:0; 
    clear:both; 
    visibility:hidden; 
    overflow:hidden; 
    content:'.';
}
.transition{
    transition:all 0.5s linear;
}
#talk #content{
    margin:0 auto; 
    width:960px; 
    overflow:hidden;
}
#talk #content #post{
    margin-bottom:15px; 
    padding-bottom:15px; 
    border-bottom:1px 
    #d4d4d4 dashed;
}
#talk #content #post textarea{
    display:block; 
    margin-bottom:10px; 
    font-size: 24px;
    padding:5px; 
    width:948px; 
    height:290px; 
    border:1px #d1d1d1 solid; 
    border-radius:5px; 
    resize:none; outline:none;
}
#talk #content #post textarea:hover{
    border:1px #d9d6c3 solid; 
    background:#fffef9;
}
#talk #content #post #postBt,#content #post #clearBt{
    margin-left:5px; 
    padding:3px; 
    float:right;
    height: 30px;
}
#talk #comment{
    overflow:hidden;
}
#talk #comment p{
    margin-bottom:10px; 
    padding:10px; 
    border-radius:5px;
}
#talk #comment p:nth-child(odd){
    border:1px solid #e3e197; 
    background:#afdfe4;
}
#talk #comment p:nth-child(even){
    border:1px solid #adcd3c; 
    background:#cde6c7;
}
#talk #comment p span{
    display:inline; 
    float:left;
}
#talk #comment p .msg{
    width:738px;
}
#talk #comment p .datetime{
    width:200px; 
    color:#999; 
    text-align:right;
}



