
#send{
	margin-left: 180px;
}
#send .sendcon{
	width: 800px;
	margin: 20px auto;
}
#send .block{
	width: 150px;
	margin: 0 auto;
}
#send .showcon{
	font-size: 18px;
	text-align: center;
	line-height: 50px;
	margin: 40px auto;
	width: 900px;
}
#send .showcon li{
	overflow: hidden;
}
#send .showcon li:first-of-type span{
	width: 100px;
	height: 30px;
	line-height: 30px;
}
#send .showcon span{
	float: left;
	display: block;
	border: 1px solid #ccc;
	box-sizing: border-box;
	width: 100px;
	height: 50px;
}
#send .showcon img{
	display: block;
	width: 100px;
	height: 50px;
}



