
.tips {
  font-size: 14px;
  color: #fff;
  margin-bottom: 5px;
}
#login {
  position: absolute;
  height: 100vh;
  width: 100%;
  background-color: #2d3a4b;
  left: 0;
  top: 0;
  z-index: 10000;
}
#login input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px #293444 inset !important;
    -webkit-text-fill-color: #fff !important;
}
#login input {
    background: transparent;
    border: 0px;
    -webkit-appearance: none;
    border-radius: 0px;
    padding: 12px 5px 12px 15px;
    color: #eeeeee;
    height: 47px;
}
#login .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;
}
#login .svg-container {
    padding: 6px 5px 6px 15px;
    color: #889aa4;
}
#login .title {
    font-size: 26px;
    font-weight: 400;
    color: #eeeeee;
    margin: 0px auto 40px auto;
    text-align: center;
    font-weight: bold;
}
#login .error {
    height: 40px;
    line-height: 40px;
    text-align: center;
    margin-bottom: 20px;
    color: #FF4949;
}
#login .login-form {
    position: absolute;
    left: 0;
    right: 0;
    width: 400px;
    padding: 35px 35px 15px 35px;
    margin: 120px auto;
}
#login .el-form-item {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
}
#login .forget-pwd {
    color: #fff;
}



