
#tabledrag{
  margin-left: 180px;
  background: #fafafa;
}
#tabledrag .container{
  margin-left: 10%;
  padding-top: 10px;
}
#tabledrag .flip-list-move {
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
#tabledrag .form-group{
	overflow: hidden;
}
#tabledrag .panel-body{
    float: left;
    width: 20%;
    margin-right: 20px;
    height: 55px;
}
#tabledrag .panel-body .btn-default{
		height: 50px;
		width: 50%;
    line-height: 50px;
    text-align: center;
    font-size: 20px;
    font-weight: 900;
    background: #fff;
    color:#000;
    box-shadow: 3px 3px 3px #ccc;
    border-radius: 10px;
}
#tabledrag .list-group{
	overflow: hidden;
}
#tabledrag .panel-body{
    float: left;
    width: 20%;
    margin-right: 20px;
    height: 55px;
}
#tabledrag .col-md-3{
	margin-top: 20px;
	float:left;
	width:90%;
	margin-right: 20px;
}
#tabledrag .col-md-3 li{
	font-size: 22px;
	background: #fff;
	height: 200px;
	line-height: 200px;
	border: 1px solid #000;
	width: 30%;
	border-radius: 10px;
	margin: 1%;
	box-shadow: 3px 3px 3px #ccc;
	text-align: center;
	cursor: move;
	float: left;
}
#tabledrag .no-move {
  transition: -webkit-transform 0s;
  transition: transform 0s;
  transition: transform 0s, -webkit-transform 0s;
}
#tabledrag .ghost {
  opacity: .5;
  background: #C8EBFB;
}
#tabledrag .list-group {
  min-height: 20px;
}



